@import "../../base/fn";

.weui_cells_access {
    .weui_cell:not(.no_access) {
        // cell_accesscellcellno_access
        .setTapColor;
        &:active {
            background-color: #ECECEC;
        }
    }
    a.weui_cell {
        color: inherit;
    }
    .weui_cell_ft {
        &:after {
            content: " ";
            .setArrow_Wap(right, 6px, #C8C8CD, 2px);
            top: -1px;
            margin-left:.3em;
        }
    }
}



